/* AssetDateInput.css */

.date-input-table {
    margin: 0 auto;
    border-collapse: collapse;
    width: 80%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.date-input-table tr:nth-child(odd) {
    background-color: #f2f2f2;
}

.date-input-table th, .date-input-table td {
    text-align: left;
    padding: 8px;
}

.date-input-table th {
    background-color: #4CAF50;
    color: white;
}

.date-input-table td {
    border-bottom: 1px solid #ddd;
}

.date-indicator {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 5px;
    vertical-align: middle;
}

.date-group label {
    margin-left: 10px;
}

.date-group input[type="date"] {
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 4px;
}

.date-group input[type="date"]:focus {
    border-color: #4CAF50;
}

.button-container {
    text-align: center;
    margin-top: 20px;
}
